import {
	Button, 
	Aside, 
	Message,
	Container,
	Header,
	Main,
	Footer,
	Icon,
	Menu,
	MenuItem,
	Image,
	Pagination,
	Input,
	Checkbox,
	Dialog,
	Upload,
	Select,
	Tree
} from "element-ui"
import Vue from "vue"

Vue.use(Button)
Vue.use(Aside)
Vue.use(Container)
Vue.use(Header)
Vue.use(Main)
Vue.use(Footer)
Vue.use(Icon)
Vue.use(Menu)
Vue.use(MenuItem)
Vue.use(Image)
Vue.use(Pagination)
Vue.use(Input)
Vue.use(Checkbox)
Vue.use(Dialog)
Vue.use(Upload)
Vue.use(Select)
Vue.use(Tree)

Vue.prototype.$message = Message


import * as echarts from 'echarts/core';
import {
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
	LegendComponent,
	ToolboxComponent
} from 'echarts/components';

// 标签自动布局，全局过渡动画等特性
import { LabelLayout, UniversalTransition } from 'echarts/features';
// 引入 Canvas 渲染器，注意引入 CanvasRenderer 或者 SVGRenderer 是必须的一步
import { CanvasRenderer } from 'echarts/renderers';
import {LineChart} from 'echarts/charts'

// 注册必须的组件
echarts.use([
  TitleComponent,
  TooltipComponent,
  GridComponent,
  DatasetComponent,
  TransformComponent,
	ToolboxComponent,
	LegendComponent,
  LabelLayout,
  UniversalTransition,
  CanvasRenderer,
	LineChart
]);

Vue.prototype.$charts = echarts
