import Cookies from "js-cookie"

export const ckSet = function (name, value) {
		if(!name || !value) return
		Cookies.set(name, value)
}

export const ckGet = function (name) {
	if(!name) return null
	let value = Cookies.get(name)
	return value
}

export const ckDel = function (name) {
	if(!name) return
	Cookies.remove(name)
}

export const ckSetToken = function (token) {
	ckSet("token", token)
}

export const ckGetToken = function () {
	return ckGet("token")
}

export const ckDelToken = function () {
	ckDel("token")
}

export const ckSetAccount = function (account) {
	ckSet("account", account)
}

export const ckGetAccount = function () {
	return ckGet("account")
}

export const ckSetPassword = function (password) {
	ckSet("password", password)
} 

export const ckGetPassword = function () {
	return ckGet("password")
}
