import Vue from "vue";
import VueRouter from "vue-router";
import { ckGetToken } from "@/utils/storeUtil/cookieUtil"

Vue.use(VueRouter);

const routes = [
  {
    path: "/home",
    name: "home",
    component: () => import("@/views/Home.vue"),
    redirect: "/work",
    children: [
      {
        path: "/work",
        name: "work",
        component: () => import("@/views/WorkList.vue")
      },
      {
        path: "/analyse",
        name: "analyse",
        component: () => import("@/views/Analyse.vue")
      },
      {
        path: "/category",
        name: "category",
        component: () => import("@/views/Category.vue")
      },
      {
        path: "/account",
        name: "account",
        component: () => import("@/views/Account.vue")
      },
      {
        path: "/application",
        name: "application",
        component: () => import("@/views/Application.vue")
      },
      {
        path: "/feedback",
        name: "feedback",
        component: () => import("@/views/Feedback.vue")
      }
    ]
  },
  {
    path: "/login",
    name: "login",
    component: () => import("@/views/Login.vue")
  },
  {
    path: "/detail",
    name: "detail",
    component: () => import("@/views/Detail.vue")
  },
  {
    path: '/writ/:type?',
    name: 'writ',
    component: () => import("@/views/Writ.vue"),
    redirect: to => {
      const {params} = to
      if(params.type) {
        return '/'+params.type
      }else{
        return "/addType"
      }
    },
    children: [
      {
        path: '/addType',
        name: 'addType',
        params: {type: 'addType'},
        component: () => import("@/views/WritAdd.vue")
      },
      {
        path: '/released',
        name: 'released',
        params: {type: 'released'},
        component: () => import("@/views/WritReleased.vue")
      }
    ]
  },
  {
    path: "/error",
    name: "error",
    component: () => import("@/views/Error.vue")
  },
];

const router = new VueRouter({
  // mode: "history",
  routes,
});

router.beforeEach((to, from, next) => {
  let token = ckGetToken()
  if(token) {
    console.log(to.path)
    if(to.path === "/login" || to.path === "/") {
      next("/home")
    } else {
      next()
    }
  }else {
    if(to.path === "/login"){
      next()
    }else{
      next("/login")
    }
  }
})

export default router;
